import React, { useEffect, useState } from 'react'
import { Famous } from './Famous'
import { Timer } from './Timer'
import famous from '../data/famous';
import incognito from '../assets/img/desaparecido.jpg';

export const PrincipalScreen = () => {

    const [time, setTime] = useState(10);

    const [score, setScore] = useState(0);

    const [start, setStart] = useState(false);

    const handleClickStart = () => {
        setStart(true);
    }

    return (
        <div className="container mt-4">
            {start ? (
                <>
                    {/* <Timer time={time} setTime={setTime} /> */}
                    <Famous time={time} setTime={setTime} score={score} setScore={setScore} />
                </>
            ) : (
                <>
                    <h2 className="text-center oswald">Adivina el famoso</h2>
                    <div className="mt-5 text-center">
                        <img src={incognito} width={"250px"} alt="incognito" />
                    </div>
                    <div className="mt-5 text-center">
                        <button className="btn btn-secondary" onClick={handleClickStart}>Empezar</button>
                    </div>
                </>
            )
            }
        </div>
    )
}
